import React from 'react';
import Helmet from 'react-helmet';

import Layout from '../components/layout';
import PageNavigation from '../components/page-navigation';

import { makeStyles } from '@material-ui/core/styles';
import { Accordion, AccordionSummary, AccordionDetails, Typography } from '@material-ui/core';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';

const useStyles = makeStyles(theme => ({
  heading: {
    fontSize: theme.typography.pxToRem(16),
    fontWeight: theme.typography.fontWeightBold,
  },
  paragraph: {
    display: "block",
    textAlign: "left"
  },
  accordionArea: {
    marginBottom: theme.spacing(3)
  }
}));

const Hint = () => {
  const classes = useStyles();

  return (
    <Layout>
      <section id="header">
        <div className="inner">
          <h1>ヒント・解答</h1>
        </div>
      </section>
      <Helmet title="ヒント・解答 | 氷蔵姉妹の挑戦" />
      
      <section id="one" className="main style1">
        <div className={`grid-wrapper ${classes.accordionArea}`}>
          <div className="col-12">
            <Accordion>
              <AccordionSummary expandIcon={<ExpandMoreIcon/>} className={classes.heading}>
                問題1：水色の問題のヒント
              </AccordionSummary>
              <AccordionDetails className={classes.paragraph}>
                <Typography>
                  まずは左の絵からオルゴールを見つけよう。2つあるね。<br/>
                  大きいオルゴールと小さいオルゴールを線でつなげると、その間に1文字見つかるよ！1文字の単語の出来上がり。<br/>
                  <br/>
                  次に、星を見つけよう。3つあるね。<br/>
                  それを、大きいものから小さいものへ順に繋いでいくわけだけど…<br/>
                  ん？一番大きい星の絵はわかるけど、残り2つはどっちが大きいんだ？こんなわかりづらい絵を用意して！<br/>
                  <br/>
                  いえいえ、これら3つは太陽系の星。そしてその大きさの違いは歴然のはず。<br/>
                  大きい星から中くらいの星に繋いで1文字。中くらいの星から小さい星に繋いで1文字。<br/>
                  これで、2文字からなる単語が手に入るよ。<br/>
                  <br/>
                  あとは、1文字の単語と2文字の単語をつなげて読めば、答えとなる3文字からなる単語の出来上がり！
                </Typography>
              </AccordionDetails>
            </Accordion>
            <Accordion>
              <AccordionSummary expandIcon={<ExpandMoreIcon/>} className={classes.heading}>
                問題2：緑色の問題のヒント
              </AccordionSummary>
              <AccordionDetails className={classes.paragraph}>
                <Typography>
                  図1から下の影に合う図形を選んで、当てはめてみよう。<br/>
                  頭の中でやるのが難しい場合は、印刷してハサミで図形を切り出すのもアリだね。<br/>
                  矢印の影に当てはまる図形は2つあるけど、図形の中の文字の向きを考えると…？<br/>
                  <br/>
                  影に図形が当てはまったら、当てはまった図形の謎を解く番だね。<br/>
                  印刷してハサミを使っていればわかりやすいかも！<br/>
                  それぞれの図形の中には文字がいくつか書いてあって、それ単体では意味が分からないんだけど、<br/>
                  影に当てはめてみると、ん？横に読めそうだぞ。<br/>
                  <br/>
                  謎が解けたら図2の出番。<br/>
                  絵と文字が混ざった表になっている。<br/>
                  謎を解くと、○○の絵がポイントだってことが分かるね。表の中に2つあるよ。<br/>
                  その絵の○を見ると、漢字が得られる。○○の絵が2つだから、漢字も2つ。<br/>
                  左から読むと、ひらがな(またはカタカナ)で4文字の言葉が得られるよ。
                </Typography>
              </AccordionDetails>
            </Accordion>
            <Accordion>
              <AccordionSummary expandIcon={<ExpandMoreIcon/>} className={classes.heading}>
                問題3：オレンジ色の問題のヒント
              </AccordionSummary>
              <AccordionDetails className={classes.paragraph}>
                <Typography>
                  まずはAとBを知る必要がありそうだ。左の迷路に「Aスタート/ゴール」「Bスタート/ゴール」があるから、それぞれ解いてみよう！<br/>
                  最短経路を通る必要があるけど、それほど難しくないよ。<br/>
                  解くと、AもBも、4文字のひらがなが手に入る。<br/>
                  「並べ替えて言葉を作り」と書いてあるから、それぞれ並べ替えないといけないんだけど、<br/>
                  集めたひらがなを眺めていると、対になる2つの言葉ができるってわかるはずだよ！<br/>
                  <br/>
                  さて、AとBの言葉が分かったから、下の4つの「形」の中から、AとBにピッタリな図形を選ぼう。<br/>
                  AとBに関するイベントで…これに書いて…飾ったりするよね…？<br/>
                  その「形」の中に書いてあるひらがなが答え！<br/>
                  答えは意味のある言葉にならないんだけど、とある都合からなんだ。ごめんね。
                </Typography>
              </AccordionDetails>
            </Accordion>
            <Accordion>
              <AccordionSummary expandIcon={<ExpandMoreIcon/>} className={classes.heading}>
                最終問題：紫色の問題のヒント
              </AccordionSummary>
              <AccordionDetails className={classes.paragraph}>
                <Typography>
                  Twitterユーザ「frig7nazo88」のツイートを探そう。<br/>
                  Twitterで、"@frig7nazo88"で検索すれば見つかるよ。<br/>
                  <br/>
                  さて、そのツイートには、言葉の作り方として、いろんな色の数字が3行書いてある。<br/>
                  <br/>
                  いま「いろんな色」と書いたけど水色、緑色、オレンジ色の3色だけだ。<br/>
                  そういえば、問題1～3にはそれぞれ「○○色の問題」と書いてあったな…？<br/>
                  そして、「答えは最終問題で使う」とも書いてあったな？<br/>
                  <br/>
                  では数字はどういう意味だろう。<br/>
                  そういえば、各問題の答えは、ひらがなで数文字の言葉だったな？
                </Typography>
              </AccordionDetails>
            </Accordion>
          </div>
        </div>
        <div className={`grid-wrapper ${classes.accordionArea}`}>
          <div className="col-12">
            <Accordion>
              <AccordionSummary expandIcon={<ExpandMoreIcon/>} className={classes.heading}>
                問題1：水色の問題の答え
              </AccordionSummary>
              <AccordionDetails className={classes.paragraph}>
                <Typography>
                  『こおり』<br/>
                  <br/>
                  2つのオルゴールをつなげて得られる言葉が「こ」(子)。
                  星は、絵ではなく実際の大きさに従って、太陽→土星→地球とつなげると「おり」(檻)。
                  この2つの単語をつなげて読んで、「こおり」(氷)が答えです。
                </Typography>
              </AccordionDetails>
            </Accordion>
            <Accordion>
              <AccordionSummary expandIcon={<ExpandMoreIcon/>} className={classes.heading}>
                問題2：緑色の問題の答え
              </AccordionSummary>
              <AccordionDetails className={classes.paragraph}>
                <Typography>
                  『あいぞう』<br/>
                  <br/>
                  図1から下の影に当てはまる、矢印、平行四辺形、角丸四角形を選ぶ。矢印は、当てはめた時に文字が逆さまにならない方を選ぶ。<br/>
                  当てはめた後、図形に描かれている文字を横向きに読むと、<br/>
                  「答えはめんのえの上」(答えは麺の絵の上)<br/>
                  図2にはラーメンの絵が2箇所あるので、その上のマスを左から読んで、「あいぞう」(愛蔵)が答えです。
                </Typography>
              </AccordionDetails>
            </Accordion>
            <Accordion>
              <AccordionSummary expandIcon={<ExpandMoreIcon/>} className={classes.heading}>
                問題3：オレンジ色の問題の答え
              </AccordionSummary>
              <AccordionDetails className={classes.paragraph}>
                <Typography>
                  『れいこす』<br/>
                  <br/>
                  AスタートからAゴールを最短経路でたどると、その途中で拾えるひらがなは「おめひり」。<br/>
                  BスタートからBゴールを最短経路でたどると、その途中で拾えるひらがなは「ひしこぼ」。<br/>
                  それぞれ並べ替えて、Aは「おりひめ」(織姫)、Bは「ひこぼし」(彦星)。<br/>
                  <br/>
                  織姫と彦星から連想される「形」として、七夕で願い事を書く短冊の形をしている一番左の図形が答えです。<br/>
                  <br/>
                  ヒントにも書きましたが、この問題だけ答えが意味のある単語にならなくてごめんなさい。
                </Typography>
              </AccordionDetails>
            </Accordion>
            <Accordion>
              <AccordionSummary expandIcon={<ExpandMoreIcon/>} className={classes.heading}>
                最終問題：紫色の問題の答え
              </AccordionSummary>
              <AccordionDetails className={classes.paragraph}>
                <div>
                  『れいとうこ』<br/>
                  <br/>
                  言葉の作り方として並んでいる色がついた数字。<br/>
                  これは、色がどの問題か (水色 = 問題1, 緑 = 問題2, オレンジ = 問題3) を表し、<br/>
                  数字がその問題の答えの何文字目かを表す。<br/>
                  <br/>
                  この法則に従って文字を当てはめてみると、<br/><br/>
                  <ul>
                    <li>れいぞうこ</li>
                    <li>こおり</li>
                    <li>あいす</li>
                  </ul>
                  となる。<br/>
                  また、下の記述から、答えが5文字であることと、「れいぞうこの一部だったりしますね。」ということもわかるので、<br/>
                  答えは「れいとうこ」(冷凍庫)です。
                </div>
              </AccordionDetails>
            </Accordion>
          </div>
        </div>
        <div className="grid-wrapper">
          <div className="col-12">
            <PageNavigation prev="/question/" />
          </div>
        </div>
      </section>
    </Layout>
  );
};

export default Hint;
